Container { padding-top: 40px; }
body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.homeImg{
    display: flex;
    justify-content: center;
    align-items: center;
    background: url(/landing-page/static/media/1.17c297e8.jpeg) no-repeat center center/cover;
    height: 100vh;
}
.bg{
    background-color: #15102c;
    color: #fa741b;
}

.form{
    background: rgba(168, 167, 167,0.4);
    background-size: 40px 60px;
    width: 100% ;
}
.margin{
    margin: 5% 0;
}
.parallax{
    background:rgba(0, 0, 0, 0.6) url(/landing-page/static/media/2.996ab4fd.jpeg) no-repeat fixed center/cover;
    background-blend-mode: darken ;
}

.parallax-inner{
    padding-top: 10%;
    padding-bottom: 10%;
    padding-left: 20%;
    padding-right: 20%;
    text-align: center;
    color: #ffffff;
    font-size: 1.2rem;
}
h2{
    text-align: center;
    padding: 25px ;
    margin-bottom: 0;
    color: #fa741b;
}
h5{
    color: #fa741b;
}
.cardX{
    width: 70%;
    min-height: 300px;
    margin:auto;
    border-radius: 20px;
    background-color: #15102e;
    border: solid 2px #fa741b;
    overflow: hidden;
}

.card-title{
    margin-left: 1rem;
}

.card-text{
    color: white;
    margin: 1rem;
}

.row{
    margin-right: 0;
    margin-left: 0;
}

@media only screen and (max-width:300px){
    .card p{
        display: none;
    }
    .card{
        padding: 70px 0;
    }
}
